.page-header {
  display: flex;
  flex-wrap: wrap;
  width: 1411px;
  margin: 0 auto;
  padding-top: 34px;
}

.page-header__logo {
  display: flex;
  margin-right: auto;
  color: $white;
}

.page-header__logo-name {
  font-family: RobotoCondensed-Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  margin: 0;
  padding-left: 17px;
  padding-top: 4px;
}

.page-header__logo-translation {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
  margin: 0;
  padding-left: 17px;
  padding-top: 2px;
}

.page-header__contacts {
  display: flex;
  margin-top: 15px;
}

.page-header__contacts a {
  font-family: 'ptmono', Arial, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  text-decoration: none;
  color: $white;
}

.page-header__contacts--mail {
  position: relative;
  padding-left: 39px;
  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: 0px;
    width: 19px;
    height: 14px;
    background-image: url("../img/icon-mail.svg");
  }
}

.page-header__contacts--tel {
  position: relative;
  padding-left: 34px;
  margin-left: 88px;
  &::before {
    content: "";
    position: absolute;
    top: 0px;
    left: 0px;
    width: 18px;
    height: 18px;
    background-image: url("../img/icon-phone.svg");
  }
}

.page-header__order-call {
  display: block;
  box-sizing: border-box;
  height: 45px;
  font-family: 'RobotoCondensed-Regular', Arial, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  color: $white;
  padding: 14px 28px;
  margin-top: 3px;
  margin-left: 48px;
  border: 1px solid $white;
  border-radius: 8px;
}

